@import url(https://fonts.googleapis.com/css?family=Exo+2);
.toggle-button {
    background: transparent;
    border: none;
    cursor: pointer;
}

.toggle-button:focus {
    outline:none;
}

.toggle-button i {
    font-size: 1.3rem;
    color: rgb(228, 228, 228);
}

.toggle-button i:hover {
    color: #00af83;
}
.toolbar {
    width: 100%;
    height: 60px;
    background: transparent;  /* fallback for old browsers */
    z-index: 100;
    box-shadow: 0 10px 6px -6px rgb(0, 0, 0);
    font-family: 'Baron Neue',sans-serif;
}

.toolbar-logo {
    margin-left: 1rem;
}

.toolbar-nav {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 8rem;
}

.spacer {
    flex: 1 1;
}
.toolbar-nav-items ul {
    list-style-type: none;
    padding: 0;
    display: flex;
} 

.toolbar-nav-items li {
    padding: 0 0.5rem;
}

.toolbar-logo a {
    text-decoration: none;
    color: rgb(216, 216, 216);
    font-size: 2rem;
}

.toolbar-logo a:hover {
    color: #00af83;
}


.toolbar-nav-items a {
    color: rgb(216, 216, 216);
    text-decoration: none;
}

.toolbar-nav-items a:hover,
.toolbar-nav-items a:active {
    color: #00af83;
}

@media screen and (max-width: 768px) {
    .toolbar-nav-items {
        display: none;
    }
    .toolbar-logo a {
        display: none;
    }
    .toolbar-nav {
        padding-left: 2rem;
    }

}

@media screen and (min-width: 769px) {
    .toolbar-toggle-button {
        display: none;
    }  
}


.side-drawer {
    height: 100%;
    background: rgb(235, 235, 235);
    position: fixed;
    top: 0;
    left: 0;
    width: 65%;
    max-width: 400px;
    z-index: 200;
    overflow-x: hidden;
    padding-top: 60px;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    font-family: 'Baron Neue',sans-serif;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    padding: 0.5rem;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.side-drawer a:hover {
    color: #00af83;
}

.side-drawer a:hover li {
    background-color: #111112;  
}

.side-drawer a {
    color: #111112;
    text-decoration: none;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}


@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}

.intro {
    height: 100%;
    width: 100%;
    margin: auto;
    display: table;
    font-family: 'Baron Neue',sans-serif;
    background: #111112;
    background: -webkit-linear-gradient(90deg,#111112,#232526);
    background: -webkit-linear-gradient(left,#111112,#232526);
    background: linear-gradient(90deg,#111112,#232526);
    border-bottom: 5px solid #00af83;
    outline: none;
}

.intro .inner {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
}

.content {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    padding-bottom: 5%;
    
}

.content h1 {
    font-size: 4rem;
    color: #00af83;
    margin: 0;
}

.content h1 i {
    color: #00af83;
    font-size: 6rem;
    width: 100%;
}

.content h2 {
    margin: 1.8rem 0;
    font-weight: 100;
}

.skills-container {
    display: flex;
    flex-flow: row wrap;
    width: 80%;
    align-content: center;
    justify-content: space-evenly;
    margin: 1.8rem auto;
}

.skills-container .skill-cards {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
    box-sizing: border-box;
    flex-basis: 5%;
    min-width: 100px;
}

.skills-container .skill-cards:hover *{
    color: #00af83;
}

.skills-container .skill-cards i {
    font-size: 3em;
    -webkit-transition: all ease 300ms;
    transition: all ease 300ms;
}

.skills-container .skill-cards h3 {
    font-size: 1.2em;
    font-weight: 100;
    margin: auto;
    -webkit-transition: all ease 300ms;
    transition: all ease 300ms;
}

.arrow.bounce a { 
    color: rgb(216, 216, 216); 
    text-decoration: none;
    -webkit-transition: color ease 300ms;
    transition: color ease 300ms;
}

.arrow.bounce a:hover { 
    color: #00af83; 
}

.arrow {
    text-align: center;
    padding-top: 10%;
}
.bounce {
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
}
  
@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
}
  
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
}

/* Media Queries */
@media screen and (max-width: 900px){
    .content {
