.projects-body {
    width: 100%;
    margin: auto;
    display: table;
    background: rgb(243, 243, 243);
    font-family: 'Baron Neue',sans-serif;
    border-bottom: 5px solid #00af83;
    color: #0b0b0c;
}

.projects-body h1 {
    text-transform: uppercase;
    font-size: 4rem;
    color: #0b0b0c;
    margin: 2rem 0;
    padding: 0 0 2rem 0;
    text-align: center;
    position: relative;
}

.projects-body h1:after {
    content: '';
    position: absolute;
    width: 80px;
    height: 5px;
    margin-left: -40px;
    left: 50%;
    bottom: 0px;
    background-color: #00af83;
}

.projects-container {
    display: flex;
    flex-flow: row wrap;
    width: 80%;
    align-content: center;
    justify-content: space-evenly;
    margin: 3rem auto;
}

.projects-container h2 {
    color: #0b0b0c;
}

.projects-container .overlay {
    width: 100%;
    height: 30%;
    position: absolute;
    margin-top: 300px;
    padding: 0.8rem 0;
    background-color: rgba(0,0,0,0.4);
}

.projects-container .project-card1,
.projects-container .project-card2,
.projects-container .project-card3 {
    width: 450px;
    height: 270px;
    margin-bottom: 2rem;
    background-size: cover;
    background-position: center;
    text-align: center;
    position: relative;
    -webkit-transition: -webkit-filter 0.3s ease-in-out;
    transition: -webkit-filter 0.3s ease-in-out;
    transition: filter 0.3s ease-in-out;
    transition: filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
    overflow: hidden;
}

.projects-container .project-card1:hover .overlay ,
.projects-container .project-card2:hover .overlay ,
.projects-container .project-card3:hover .overlay {
    margin-top: 190px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
} 

.projects-container .project-card1:hover,
.projects-container .project-card2:hover,
.projects-container .project-card3:hover {
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}

.projects-container .project-card1 {
    background-image: url(/website-portfolio/static/media/brain.e40d110e.PNG);
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.projects-container .project-card2 {
    background-image: url(/website-portfolio/static/media/brain.e40d110e.PNG);
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.projects-container .project-card3 {
    background-image: url(/website-portfolio/static/media/brain.e40d110e.PNG);
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.overlay a {
    display: inline-block;
    font-size: 1.2rem;
    color: #fff;
    padding: 10px 20px;
    text-decoration: none;
    border: 3px solid #fff;
    border-radius: 5px;
    margin: 0 5px;
}

.overlay a:hover {
    color: #00af83;
    border: 3px solid #00af83;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

@media screen and (max-width: 900px){
