.contact-body {
    margin: auto;
    width: 100%;
    justify-content: center;
    padding-top: 1rem;
    font-family: 'Baron Neue',sans-serif;
    text-align: center;
}

.contact-body h1 {
    text-transform: uppercase;
    font-size: 4rem;
    color: #fff;
    margin: 2rem 0;
    padding: 0 0 2rem 0;
    text-align: center;
    position: relative;
}

.contact-body h1:after {
    content: '';
    position: absolute;
    width: 80px;
    height: 5px;
    margin-left: -40px;
    left: 50%;
    bottom: 0px;
    background-color: #00af83;
}

.btn {
    display: inline-block;
    width: 90px;
    height: 90px;
    background: #f1f1f1;
    margin: 10px;
    border-radius: 30%;
    box-shadow: 0 8px 15px -5px black;
    color: #0b0b0c;
    overflow: hidden;
    position: relative;
}

.btn i {
    line-height: 90px;
    font-size: 2rem;
    -webkit-transition: 0.2s linear;
    transition: 0.2s linear;
}

.btn:hover i {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    color: #f1f1f1;
}

.btn::before {
    content: '';
    position: absolute;
    width: 120%;
    height: 120%;
    background: #00af83;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    left: -110%;
    top: 90%;
}

.btn:hover::before {
    -webkit-animation: bganim 0.7s 1;
            animation: bganim 0.7s 1;
    top: -10%;
    left: -10%;
}

@-webkit-keyframes bganim {
    0% {
        top: 90%;
        left: -110%;  
    }
    50% {
        top: -30%;
        left: 10%;  
    }
    100% {
        top: -10%;
        left: -10%;
    }
}

@keyframes bganim {
    0% {
        top: 90%;
        left: -110%;  
    }
    50% {
        top: -30%;
        left: 10%;  
    }
    100% {
        top: -10%;
        left: -10%;
    }
}

@media screen and (max-width: 900px){
    .btn {
        width: 70px;
        height: 70px;
    }
    .btn i {
        line-height: 70px;
        font-size: 1.8rem;
    }
}

